import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { CustGroupInfoBean } from './../bean/custom-group.bean';
import { CusOperationHttpService } from './../../http/custom-operation/custom-operation.http.service';
import { Message, ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import * as _ from 'lodash';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';


@Component({
    selector: 'custom-group-examine',
    templateUrl: './custom-group-examine.component.html',
    styleUrls: ['./custom-group-examine.component.scss'],
    providers: [ConfirmationService, Commfunc,CusOperationHttpService],
})

export class CustomGroupExamineComponent implements OnInit {
    // 标题
    public title: any = '客户群';
    // 中间量
    code:any;
    public contactRoleName: any = []; //群联系人
    public introducerRoleName: any = []; //群介绍人
    public mentorRoleName: any = []; //指导师傅
    public contactArr: any = [];//群联系人
    public introducerArr: any = [];//群介绍人
    public mentorRoleArr: any = [];//指导师傅

    public groupState: any;// = GroupState;//群状态
    public groupType: any;// = GroupTypeSave;//群类型
    public groupStateCheck: any = '3';//群状态
    public groupTypeCheck: any = '';//群类型

    // session
    public orgName: any = '';
    public orgIdTemp: any = '';
    public orgNameArr: any = [];

    // 传参
    public groupId: any = '';
    public data: any = {};
    // bean
    public custGroupInfoBean: CustGroupInfoBean = new CustGroupInfoBean();//客户群信息

    // 控件
    public msgs: Message[] = [];

    constructor(
        public routerInfo: ActivatedRoute,
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
        public router: Router,
        public commfunc: Commfunc,
    ) {
        this.codeValues() //调用方法，获取全部码值
        //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
        this.groupState = this.code['GroupState']; 
        this.groupType = this.code['GroupTypeSave'];
        
    }
    ngOnInit() {
        this.preSet();
        this.selectCustGroupInfo();
    }
    //码值
codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code= ret //code是自己声明的变量
    }
    preSet() {
        // 初始化
        // session
        let session = window.sessionStorage;
        let data = session.getItem('commonHeader');
        if (!data) {
            this.router.navigate(["/login"]);
            return;
        }
        let mesg = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (!mesg) {
        }
        let ch = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        this.orgName = ch['orgName'];//机构名
        this.orgIdTemp = mesg['orgId'];//机构id
        // 机构列表
        let tempArr = [{ label: '请选择', value: '' }];
        tempArr.push({ label: this.orgName, value: this.orgIdTemp });
        this.orgNameArr = _.clone(tempArr);

        // 接收route传参
        if (this.routerInfo.snapshot.queryParams['id']) {
            this.groupId = this.routerInfo.snapshot.queryParams['id'];
        }
        if (this.routerInfo.snapshot.params['id']) {
            this.groupId = this.routerInfo.snapshot.params['id'];
        }
        if (this.routerInfo.snapshot.params['groupId']) {
            this.groupId = this.routerInfo.snapshot.params['groupId'];
        }
        this.custGroupInfoBean.groupId = this.groupId;//群号
        //审批人缺参注意-------------------------------
        
        //审批人缺参注意-------------------------------
    }
    selectCustGroupInfo() {
        // 客户群信息查询
        this.httpService.selectCustGroupInfo(this.custGroupInfoBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.custGroupInfoBean = data;
                this.custGroupInfoBean.contactList = data.contactList;
                this.custGroupInfoBean.mentorList = data.mentorList;
                this.title = `${this.custGroupInfoBean.groupName}客户群`;//title
                this.groupTypeCheck = _.clone(this.custGroupInfoBean.groupType);//群类型绑定

                this.groupStateCheck = _.clone(this.custGroupInfoBean.groupStatus);//群状态绑定
                this.contactArr = data.contactList;//群联系人
                this.introducerArr = data.introducerList;//群介绍人
                this.mentorRoleArr = data.mentorList;//指导师傅
                this.selectFor();//查询赋值
                this.adminSelect();//管理人员查询格式
            }
            else {
                this.msgs = [];
                data.returnCode.code ? data.returnCode.code : '查询失败！';
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
        })
    }
    adminSelect() {
        // 管理人员查询格式

        let contactArr = [];
        let introducerArr = [];
        let mentorRoleArr = [];
        for (let i in this.contactArr) {
            contactArr.push(
                {
                    userId: _.clone(this.contactArr[i].roleId),
                    userName: _.clone(this.contactArr[i].roleName),
                    groupZone: _.clone(this.contactArr[i].groupZone),
                    statusId: '2',
                }
            )
        }
        for (let i in this.introducerArr) {
            introducerArr.push(
                {
                    userId: _.clone(this.introducerArr[i].roleId),
                    userName: _.clone(this.introducerArr[i].roleName),
                    groupZone: _.clone(this.introducerArr[i].groupZone),
                    statusId: '2',
                }
            )
        }
        for (let i in this.mentorRoleArr) {
            mentorRoleArr.push(
                {
                    userId: _.clone(this.mentorRoleArr[i].roleId),
                    userName: _.clone(this.mentorRoleArr[i].roleName),
                    groupZone: _.clone(this.mentorRoleArr[i].groupZone),
                    statusId: '2',
                }
            )
        }
        this.contactArr = _.clone(contactArr);
        this.introducerArr = _.clone(introducerArr);
        this.mentorRoleArr = _.clone(mentorRoleArr);
    }
    selectFor() {
        // 查询赋值

        this.contactRoleName = [];
        this.introducerRoleName = [];
        this.mentorRoleName = [];
        for (let i in this.contactArr) {
            if (this.contactArr[i].roleName) {
                this.contactRoleName.push(this.contactArr[i].roleName);
            }
        }
        this.contactRoleName = _.clone(this.contactRoleName)
        for (let i in this.introducerArr) {
            if (this.introducerArr[i].roleName) {
                this.introducerRoleName.push(this.introducerArr[i].roleName);
            }
        }
        this.introducerRoleName = _.clone(this.introducerRoleName)
        for (let i in this.mentorRoleArr) {
            if (this.mentorRoleArr[i].roleName) {
                this.mentorRoleName.push(this.mentorRoleArr[i].roleName);
            }
        }
        this.mentorRoleName = _.clone(this.mentorRoleName)
    }
    returnGroupView() {
        // 返回我的客户群页面
        // let param = {groupId:this.groupId,applyType:'1',taskCategoryId:''};
        // this.data = JSON.stringify(param)
        this.data = `{"groupId":${this.groupId},"applyType":"1","taskCategoryId":""}`;
        this.confirmationService.confirm({
            message: '跳转到审批',
            accept: () => {
                this.router.navigate(['/pages/tzb/workflow/custom-workflow/custom-group', { scheduleId: '', taskCategoryId: '', data: this.data }]);
            }
        });
    }
}